import http from 'http';

enum StatusType {
  INFO = '1',
  SUCCESS = '2',
  REDIRECT = '3',
  CLIENTERROR = '4',
  SERVERERROR = '5',
}

export type StatusCode = string | number;

export interface StatusCodeMap {
  [key: number]: string;
  [key: string]: StatusCode;
}

export class Status {
  CONTINUE = 100;

  SWITCHING_PROTOCOLS = 101;

  PROCESSING = 102;

  EARLY_HINTS = 103;

  OK = 200;

  CREATED = 201;

  ACCEPTED = 202;

  NON_AUTHORITATIVE_INFORMATION = 203;

  NO_CONTENT = 204;

  RESET_CONTENT = 205;

  PARTIAL_CONTENT = 206;

  MULTI_STATUS= 207;

  ALREADY_REPORTED = 208;

  IM_USED = 226;

  MULTIPLE_CHOICES = 300;

  MOVED_PERMANENTLY = 301;

  FOUND = 302;

  SEE_OTHER = 303;

  NOT_MODIFIED = 304;

  USE_PROXY = 305;

  TEMPORARY_REDIRECT = 307;

  PERMANENT_REDIRECT = 308;

  BAD_REQUEST = 400;

  UNAUTHORIZED = 401;

  PAYMENT_REQUIRED = 402;

  FORBIDDEN = 403;

  NOT_FOUND = 404;

  METHOD_NOT_ALLOWED = 405;

  NOT_ACCEPTABLE = 406;

  PROXY_AUTHENTICATION_REQUIRED = 407;

  REQUEST_TIMEOUT = 408;

  CONFLICT = 409;

  GONE = 410;

  LENGTH_REQUIRED = 411;

  PRECONDITION_FAILED = 412;

  PAYLOAD_TOO_LARGE = 413;

  URI_TOO_LONG = 414;

  UNSUPPORTED_MEDIA_TYPE = 415;

  RANGE_NOT_SATISFIABLE = 416;

  EXPECTATION_FAILED = 417;

  IM_A_TEAPOT = 418;

  MISDIRECTED_REQUEST = 421;

  UNPROCESSABLE_ENTITY = 422;

  LOCKED = 423;

  FAILED_DEPENDENCY = 424;

  UNORDERED_COLLECTION = 425;

  UPGRADE_REQUIRED = 426;

  PRECONDITION_REQUIRED = 428;

  TOO_MANY_REQUESTS = 429;

  REQUEST_HEADER_FIELDS_TOO_LARGE = 431;

  UNAVAILABLE_FOR_LEGAL_REASONS = 451;

  INTERNAL_SERVER_ERROR = 500;

  NOT_IMPLEMENTED = 501;

  BAD_GATEWAY = 502;

  SERVICE_UNAVAILABLE = 503;

  GATEWAY_TIMEOUT = 504;

  HTTP_VERSION_NOT_SUPPORTED = 505;

  VARIANT_ALSO_NEGOTIATES = 506;

  INSUFFICIENT_STORAGE = 507;

  LOOP_DETECTED = 508;

  BANDWIDTH_LIMIT_EXCEEDED = 509;

  NOT_EXTENDED = 510;

  NETWORK_AUTHENTICATION_REQUIRED = 511;

  STATUS_CODES = http.STATUS_CODES;

  codes: StatusCodeMap;

  statusCodes: number[];

  constructor() {
    const map = {};

    this.statusCodes = ((obj: StatusCodeMap): number[] => {
      const list: number[] = [];

      Object.keys(http.STATUS_CODES).forEach((code: string): void => {
        const message = http.STATUS_CODES[code];
        const num = Number.parseInt(code);

        if (message) {
          obj[message] = num;
          obj[message.toLowerCase()] = num;
          obj[message.toUpperCase()] = num;
          obj[num] = message;
          obj[code] = message;
        }

        list.push(num);
      });

      return list;
    })(map);

    this.codes = map;
  }

  code(input: StatusCode): StatusCode {
    if (typeof input === 'number') {
      if (this.codes[input]) return input;

      throw new Error(`Invalid status code ${input}`);
    } else if (typeof input === 'string') {
      if (isNaN(Number.parseInt(input))) {
        const code = this.codes[input.toLowerCase()];

        if (code) return code;

        throw new Error(`Invalid status message ${input}`);
      }

      return this.code(Number.parseInt(input));
    }

    throw new Error('Input must be a number or string');
  }

  isInfo(input: StatusCode): boolean {
    return this.is(StatusType.INFO, input);
  }

  isSuccess(input: StatusCode): boolean {
    return this.is(StatusType.SUCCESS, input);
  }

  isRedirect(input: StatusCode): boolean {
    return this.is(StatusType.REDIRECT, input);
  }

  isClientError(input: StatusCode): boolean {
    return this.is(StatusType.CLIENTERROR, input);
  }

  isServerError(input: StatusCode): boolean {
    return this.is(StatusType.SERVERERROR, input);
  }

  isError(input: StatusCode): boolean {
    return this.isClientError(input) || this.isServerError(input);
  }

  private is(type: StatusType, input: StatusCode): boolean {
    return this.statusCodes.includes(Number.parseInt(input.toString())) && String(input).charAt(0) === type;
  }
}

export default new Status();
